body
	&.overflow-hidden
		position: fixed
		width: 100%

.top-banner
	width: 100%
	height: 149px
	+cover
	// display: block
	display: none
	+r1
		background-position: 43% 50%
	+r2
		display: none

.header
	&.is-fixed
		.header__fixed
			position: fixed
			top: 0
			left: 0
			width: 100%
	&.is-hidden
		.header__fixed
			transform: translateY(-110%)
	&__fixed
		transition: transform 0.4s
		padding: 0 200px
		background-color: #fff
		box-shadow: 0 2px 6px rgba(159, 159, 159, 0.5)
		min-height: 80px
		z-index: 300
		min-width: 320px
		position: relative
		+r1
			padding: 0 70px 0 165px
		+r2
			min-height: 60px
			padding: 0 45px 0 0
	&__inner
		margin: 0 auto
		max-width: 1290px
		+clr

.mini-search
	position: absolute
	top: 0
	right: 0
	padding: 24px 25px 26px 10px
	background: #fff
	min-width: 70px
	+r2
		padding: 17px 15px 16px 10px
		min-width: 45px
	a
		font-size: 29px
		color: #282828
		position: absolute
		top: 24px
		right: 25px
		cursor: pointer
		+tr(color .3s)
		+r2
			top: 19px
			right: 15px
			font-size: 20px
		&:hover
			color: $orange

.logo
	+bold
	color: #282828
	white-space: nowrap
	font-size: 0
	line-height: 1
	position: absolute
	top: 10px
	left: 25px
	padding: 13px 0 7px
	+r2
		top: 0
		left: 50%
		padding: 13px 0 7px
		width: 130px
		text-align: center
		margin-left: -65px
	&__orange
		color: #ff8c61
		margin-right: -9px
		z-index: 2
	span
		display: inline-block
		vertical-align: top
		font-size: 40px
		position: relative

.btn-nav
	float: left
	width: 26px
	height: 22px
	position: relative
	cursor: pointer
	margin: 20px 10px 0
	display: none
	background: none
	+r2
		display: block
	&.is-active
		&:before, &:after
			top: 9px
			transition: transform 0.2s 0.2s, top 0.2s
		&:before
			transform: rotate(45deg)
		&:after
			transform: rotate(-45deg)
		span
			opacity: 0
			transition: opacity 0.2s
	&:before, &:after, span
		border-bottom: 4px solid #282828
		width: 100%
		position: absolute
		left: 50%
		margin-left: -13px
	&:before, &:after
		content: ""
		transition: transform 0.2s, top 0.2s 0.2s
	&:before
		top: 0
	&:after
		top: 18px
	span
		top: 9px
		transition: opacity 0.2s 0.25s


.btn-enter
	float: right
	color: #282828
	font-size: 18px
	line-height: 1
	+med
	content: ""
	position: relative
	padding: 10px 0 10px 40px
	margin: 21px 0
	min-height: 37px
	+tr(color .3s)
	+r2
		padding: 0
		margin: 17px 0 0
		min-height: 1px
	i
		font-size: 37px
		position: absolute
		top: 50%
		left: 0
		margin-top: -19px
		+r2
			font-size: 26px
			position: static
			margin: 0
	span
		+r2
			display: none
	&:hover
		color: #ff8c61


.nav
	font-size: 0
	float: left
	+r2
		position: fixed
		top: 60px
		bottom: 0
		left: 0
		width: 100%
		max-width: 300px
		background: #fff
		box-shadow: 0 5px 6px rgba(159, 159, 159, 0.5)
		overflow: hidden
		visibility: hidden
		opacity: 0
		transform: translateX(-100%)
		transition-property: opacity, visibility, transform
		transition-duration: 0.4s
	&.is-active
		+r2
			transform: translateX(0)
			opacity: 1
			visibility: visible
	&:before
		content: ""
		position: absolute
		top: -9px
		left: 0
		right: 0
		box-shadow: 0 2px 6px rgba(159, 159, 159, 0.5)
		height: 10px
		display: none
		+r2
			display: block
	&__list
		+r2
			max-height: 100%
			overflow: auto
			padding: 20px 0
	&__item
		display: inline-block
		vertical-align: top
		padding: 0 20px 0 0
		+r2
			display: block
			padding: 0
	&__link
		color: #282828
		font-size: 18px
		line-height: 1
		+med
		display: block
		position: relative
		transition: color 0.3s
		&:hover
			span
				&:before
					width: 100%
		&.is-brown
			&:hover, &.is-active
				color: #d27857
			span
				&:before
					border-color: #d27857
		&.is-purple
			&:hover, &.is-active
				color: #c0a6cd
			span
				&:before
					border-color: #c0a6cd
		&.is-yellow
			&:hover, &.is-active
				color: #f2c05e
			span
				&:before
					border-color: #f2c05e
		&.is-orange
			&:hover, &.is-active
				color: #ff8c61
			span
				&:before
					border-color: #ff8c61
		&.is-drop
			padding-right: 25px
		i
			font-size: 14px
			position: absolute
			top: 50%
			right: 0
			margin-top: -9px
			+r2
				right: 15px
		span
			display: block
			padding: 30px 0 32px
			position: relative
			+r2
				padding: 10px 15px
			&:before
				content: ""
				position: absolute
				bottom: 0
				left: 0
				width: 20px
				border-bottom: 5px solid
				transition: width 0.3s
				+r2
					display: none
